import { ChangeEvent, FC, useEffect, useState } from 'react';

import _ from 'lodash';
import Button from 'src/components/common/core/button/Button';
import Slider from 'src/components/common/core/slider/Slider';
import useSafeContext from 'src/hooks/useSafeContext';
import { getNotes } from 'src/reducers/oscillatorsSlice';
import {
    EnvelopeModule,
    OscillatorModule,
    getModule,
    getRootModuleId,
} from 'src/reducers/synthesisSlice';
import { useDebounce } from 'usehooks-ts';

import MainContext, {
    ModuleInterface,
} from '../../../context/MainContext/MainContext';

interface OscillatorProps {
    oscId: string;
    parentModuleId?: string;
    envelopeId?: string;
}
const OscillatorComponent: FC<OscillatorProps> = ({
    oscId,
    parentModuleId = getRootModuleId(),
    envelopeId,
}) => {
    const {
        state: { audioContext, modules },
        dispatch: dispatchMainCTX,
    } = useSafeContext(MainContext);
    const { currentTime } = audioContext;

    const [oscModule, setOscModule] = useState<OscillatorModule>({
        id: oscId,
        parentModuleId,
        childModuleIds: [],
        type: 'sine',
        freq: 0,
        gain: 0.5,
        pitch: 0,
        envelopeId,
    });
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const { type, note, freq, gain, pitch } = oscModule;

    const notes = getNotes();

    const { attack, decay, sustain, release } = envelopeId
        ? (getModule(envelopeId) as EnvelopeModule).envelope
        : { attack: 0, decay: 0, sustain: gain, release: 0 };

    // TODO: fix debounces usability
    const [minPitch, setMinPitch] = useState<number>(-1200);
    const [maxPitch, setMaxPitch] = useState<number>(1200);
    const debouncedMinPitch = useDebounce<number>(minPitch, 500);
    const debouncedMaxPitch = useDebounce<number>(maxPitch, 500);

    const [oscillatorState, setOscillatorState] = useState<OscillatorState>({
        oscillators: {},
        gainNode: new GainNode(audioContext, { gain }),
    });
    const { oscillators, gainNode } = oscillatorState;
    const [detuneConstantSource, setDetuneConstantSource] =
        useState<ConstantSourceNode>();

    const [inputIds, setInputIds] = useState<string[]>([]);
    const [gainInputIds, setGainInputIds] = useState<string[]>([]);
    const [freqInputIds, setFreqInputIds] = useState<string[]>([]);

    const addToList = (list: string[], toAdd: string[]) =>
        toAdd
            .filter((item) => !list.includes(item))
            .forEach((item) => list.push(item));
    const [module] = useState<ModuleInterface>({
        outputNode: gainNode,
        addInputs: (moduleIds: string[]) =>
            setInputIds((prevInputIds) => {
                const newInputIds = [...prevInputIds];
                addToList(newInputIds, moduleIds);
                return newInputIds;
            }),
        addGainInputs: (moduleIds: string[]) =>
            setGainInputIds((prevGainInputIds) => {
                const newGainInputIds = [...prevGainInputIds];
                addToList(newGainInputIds, moduleIds);
                return newGainInputIds;
            }),
        addFreqInputs: (moduleIds: string[]) =>
            setFreqInputIds((prevFreqInputIds) => {
                const newFreqInputIds = [...prevFreqInputIds];
                addToList(newFreqInputIds, moduleIds);
                return newFreqInputIds;
            }),
    });

    useEffect(() => {
        let constantSourceNode = detuneConstantSource;
        if (detuneConstantSource === undefined) {
            constantSourceNode = new ConstantSourceNode(audioContext, {
                offset: undefined,
            });
            constantSourceNode.offset.value = pitch;
            constantSourceNode.start();
            setDetuneConstantSource(constantSourceNode);
        }
        if (modules && !modules[oscId]) {
            dispatchMainCTX({
                type: 'ADD_MODULE',
                payload: { id: oscId, module },
            });
        }
    }, []);

    useEffect(() => {
        Object.values(oscillators).forEach(([oscillator]) => {
            oscillator.type = type;
        });
    }, [type]);

    useEffect(() => {
        gainNode.gain.value = gain;
    }, [gain]);

    useEffect(() => {
        if (detuneConstantSource) {
            detuneConstantSource.offset.value = pitch;
        }
    }, [detuneConstantSource, pitch]);

    const newOscillator = (
        frequency: number,
        velocity?: number,
    ): [OscillatorNode, GainNode] => {
        const oscNode = new OscillatorNode(audioContext, {
            type,
            frequency,
            detune: pitch,
        });
        oscNode.start();
        detuneConstantSource?.connect(oscNode.detune);

        const velocityGain = velocity ? 0.1 + velocity / 127 : 1;
        const oscGain = new GainNode(audioContext, { gain: 0 });
        oscGain.gain.setTargetAtTime(0, currentTime, currentTime + easing);
        oscGain.gain.linearRampToValueAtTime(
            velocityGain,
            currentTime + attack + easing,
        );
        oscGain.gain.linearRampToValueAtTime(
            sustain * velocityGain,
            currentTime + attack + decay + easing,
        );
        oscNode.connect(oscGain);
        oscGain.connect(gainNode);

        return [oscNode, oscGain];
    };

    useEffect(() => {
        console.log('FREQ', freq);
        setOscillatorState((prevOscillatorState) => {
            const newOscillators = { ...prevOscillatorState.oscillators };

            const oscNotes = !freq
                ? notes
                : { [`freq_${freq}`]: { frequency: freq } };

            Object.entries(newOscillators)
                .filter(([key]) => !oscNotes[key])
                .forEach(([key, [osc, oscGain]]) => {
                    console.log('REM', key);
                    stopOscillator({
                        oscNode: osc,
                        oscGainNode: oscGain,
                        currentTime,
                        release,
                    });
                    delete newOscillators[key];
                });

            Object.entries(oscNotes)
                .filter(([key]) => !newOscillators[key])
                .forEach(([key, { frequency, velocity }]) => {
                    console.log('ADD', key);
                    newOscillators[key] = newOscillator(frequency, velocity);
                });

            return { ...prevOscillatorState, oscillators: newOscillators };
        });
    }, [freq, notes]);

    useEffect(() => {
        inputIds.forEach((inputId, index) => {
            const module = modules[inputId];
            if (!module) {
                inputIds.splice(index, 1);
            } else {
                modules[inputId].outputNode.connect(gainNode);
            }
        });
    }, [inputIds]);

    useEffect(() => {
        gainInputIds.forEach((inputId, index) => {
            const module = modules[inputId];
            if (!module) {
                gainInputIds.splice(index, 1);
            } else {
                modules[inputId].outputNode.connect(gainNode.gain);
            }
        });
    }, [gainInputIds]);

    useEffect(() => {
        freqInputIds.forEach((inputId, index) => {
            const module = modules[inputId];
            if (!module) {
                freqInputIds.splice(index, 1);
            } else {
                console.log('connecting', oscillators, modules[inputId]);
                Object.values(oscillators).forEach(([oscillatorNode]) =>
                    modules[inputId].outputNode.connect(
                        oscillatorNode.frequency,
                    ),
                );
            }
        });
    }, [oscillators, freqInputIds]);

    const onChangeGain = (e: ChangeEvent): void => {
        let value = parseFloat((e.target as HTMLInputElement).value);
        setGain(value);
    };

    const setGain = (value: number): void => {
        setOscModule((prevOscModule) => {
            const newOscModule = _.cloneDeep(prevOscModule);
            return { ...newOscModule, gain: value };
        });
    };

    const onChangePitch = (e: ChangeEvent): void => {
        const value = parseFloat((e.target as HTMLInputElement).value);
        setOscModule((prevOscModule) => {
            const newOscModule = _.cloneDeep(prevOscModule);
            return { ...newOscModule, pitch: value };
        });
    };

    const onResetPitch = (id: string, value: number): void => {
        setOscModule((prevOscModule) => {
            const newOscModule = _.cloneDeep(prevOscModule);
            return { ...newOscModule, pitch: value };
        });
    };

    const onChangeMinPitch = (e: ChangeEvent) => {
        const { value } = e.target as HTMLInputElement;
        setMinPitch(value ? parseFloat(value) : 0);
    };
    const onChangeMaxPitch = (e: ChangeEvent) => {
        const { value } = e.target as HTMLInputElement;
        setMaxPitch(value ? parseFloat(value) : 0);
    };

    const onChangeFreq = (e: ChangeEvent) => {
        const { value } = e.target as HTMLInputElement;
        setOscModule((prevOscModule) => {
            const newOscModule = _.cloneDeep(prevOscModule);
            return { ...newOscModule, freq: parseFloat(value) };
        });
    };

    // eslint-disable-next-line no-undef
    const onChangeType = (waveType: OscillatorType) => {
        if (type !== waveType) {
            setOscModule((prevOscModule) => {
                const newOscModule = _.cloneDeep(prevOscModule);
                return { ...newOscModule, type: waveType };
            });
        }
    };

    return (
        <div style={{ display: 'flex' }}>
            <div>
                <span>Freq:</span>
                <input
                    type="number"
                    id={`${oscId}_freq`}
                    value={freq}
                    onChange={onChangeFreq}
                    min={0}
                    max={20000}
                />
            </div>
            <Slider
                id={`${oscId}_gain`}
                label="gain"
                value={gain}
                onChange={onChangeGain}
                onSliderReset={(id, value) => setGain(value)}
                resetValue={0.2}
                max={6000}
                min={0}
                step={0.01}
            />
            <div>
                <Slider
                    id={`${oscId}_pitch`}
                    label="pitch"
                    value={pitch}
                    onChange={onChangePitch}
                    onSliderReset={onResetPitch}
                    resetValue={0}
                    min={debouncedMinPitch}
                    max={debouncedMaxPitch}
                    step={0.01}
                />
                <span>Min:</span>
                <input
                    type="number"
                    id={`${oscId}_max_pitch`}
                    value={minPitch}
                    onChange={onChangeMinPitch}
                    min={-7200}
                    max={7200}
                />
                <span>Max:</span>
                <input
                    type="number"
                    id={`${oscId}_max_pitch`}
                    value={maxPitch}
                    onChange={onChangeMaxPitch}
                    min={-7200}
                    max={7200}
                />
            </div>
            <div style={{ display: 'flex', flexDirection: 'column' }}>
                {waveTypes.map((waveType) => (
                    <Button
                        id={`${oscId}_${waveType}_type`}
                        key={waveType}
                        title={waveType}
                        buttonKind={waveType === type ? 'active' : undefined}
                        onClick={() => onChangeType(waveType)}
                    />
                ))}
            </div>
        </div>
    );
};

export default OscillatorComponent;

const easing = 0.005;
// eslint-disable-next-line no-undef
const waveTypes: OscillatorType[] = ['sine', 'triangle', 'square', 'sawtooth'];

interface StopOscillatorParams {
    oscNode: OscillatorNode;
    oscGainNode: GainNode;
    currentTime: number;
    release: number;
}
const stopOscillator = ({
    oscNode,
    oscGainNode,
    currentTime,
    release,
}: StopOscillatorParams): void => {
    oscGainNode.gain.cancelScheduledValues(currentTime);
    oscGainNode.gain.setTargetAtTime(0, currentTime, release / 3 + easing); // Exponential ramp to target. After time/3 around 95% close to target
    setTimeout(() => {
        oscNode.stop();
        oscNode.disconnect();
        oscGainNode.disconnect();
    }, 10 * release * 1000 + 1000);
};

export interface OscillatorState {
    oscillators: Record<string, [OscillatorNode, GainNode]>;
    gainNode: GainNode;
}
