import { useEffect, useMemo } from 'react';

import _ from 'lodash';
import MainContext from 'src/components/modules/context/MainContext/MainContext';
import useSafeContext from 'src/hooks/useSafeContext';
import { Oscilloscope } from 'webaudio-oscilloscope';

import OscillatorComponent from '../oscillator/OscillatorComponent';

const MainAudioComponent = () => {
    const oscIds = [
        useMemo(() => _.uniqueId('oscillator_'), []),
        useMemo(() => _.uniqueId('oscillator_'), []),
        useMemo(() => _.uniqueId('oscillator_'), []),
    ];

    const {
        state: { audioContext, mainConnection, modules },
    } = useSafeContext(MainContext);

    useEffect(() => {
        const cvs1 = document.querySelector('.oscilloscope1');
        const oscilloscope1 = new Oscilloscope(
            audioContext,
            mainConnection,
            cvs1,
            null,
            32768,
        );
        oscilloscope1.start();
    }, []);

    useEffect(() => {
        const osc1 = modules[oscIds[0]];
        if (osc1) {
            osc1.outputNode.connect(mainConnection);
            if (osc1.addGainInputs) {
                osc1.addGainInputs([oscIds[1]]);
            }
            if (osc1.addFreqInputs) {
                osc1.addFreqInputs([oscIds[2]]);
            }
        }
    }, [modules]);

    return (
        <div>
            <h1>Main Module</h1>
            <OscillatorComponent oscId={oscIds[0]} />
            <OscillatorComponent oscId={oscIds[1]} />
            <OscillatorComponent oscId={oscIds[2]} />
        </div>
    );
};

export default MainAudioComponent;
