import { Provider } from 'react-redux';
import Section from 'src/components/common/core/section';
import MainAudioComponent from 'src/components/modules/components/core/main/MainAudioComponent';
import { MainContextProvider } from 'src/components/modules/context/MainContext/MainContext';
// import { ModuleContextProvider } from 'src/components/modules/context/ModuleContext';
// import OscillatorModule from 'src/components/modules/generator/oscillatorModule/OscillatorModule';
import Freezer from 'src/components/specific/freezer';
// import OscillatorsPanel from 'src/components/specific/oscillatorsPanel';
import SynthPanel from 'src/components/specific/synthPanel/SynthPanel';
import { MainAudioContextProvider } from 'src/context/MainAudioContext';

import store from './store';

const App = () => {
    return (
        <Provider store={store}>
            <MainAudioContextProvider>
                <Section>
                    <h1 className="center__text">ModuSynth</h1>
                </Section>
                <Section>
                    <SynthPanel />
                </Section>
                <Section>
                    <Freezer />
                </Section>
                <Section>{/* <OscillatorsPanel /> */}</Section>
                {/* <ModuleContextProvider>
                    <OscillatorModule />
                </ModuleContextProvider> */}
            </MainAudioContextProvider>

            <MainContextProvider>
                <MainAudioComponent />
            </MainContextProvider>
            <div>
                <canvas
                    className="oscilloscope1"
                    width="2000px"
                    height="300px"
                ></canvas>
                <canvas
                    className="oscilloscope2"
                    width="2000px"
                    height="300px"
                ></canvas>
                <canvas
                    className="oscilloscope3"
                    width="2000px"
                    height="500px"
                ></canvas>
            </div>
        </Provider>
    );
};

export default App;
