import { PayloadAction, createSlice } from '@reduxjs/toolkit';
import _ from 'lodash';
import { useAppSelector } from 'src/app/hooks';
import { NoteName } from 'src/types/notes';
import { Envelope } from 'src/types/oscillator';

export type RootModuleId = 'root';
const ROOT_MODULE_ID: RootModuleId = 'root';

export type ID = string;
export interface Note {
    noteName: NoteName;
    octaveNum: number;
}

/**
 * Generators: synthesis module that creates sound (oscillator, sample, etc)
 */
export type GeneratorModule = CombinatorModule | OscillatorModule;
/**
 * Combiners: combine generators together (mixer, crossfade, etc)
 */
export type CombinerModule = CombinatorModule;
/**
 * Modifiers:
 */
export type ModifierModule =
    | EnvelopeModule
    | ModulatorModule
    | CombinatorModule
    | OscillatorModule;

export interface Module {
    id: string;
    parentModuleId: string;
    childModuleIds: string[];
}

export interface OscillatorModule extends Module {
    // eslint-disable-next-line no-undef
    type: OscillatorType;
    note?: Note | undefined;
    freq?: number | undefined;
    gain: number;
    pitch: number;
    envelopeId?: ID;
}

export interface CombinatorModule extends Module {
    generators: Record<ID, GeneratorModule>;
    envelopeId?: Envelope;
    gain: number;
    gainModifiers: Record<ID, ModifierModule>;
    freqModifiers: Record<ID, ModifierModule>;
}

export interface EnvelopeModule extends Module {
    envelope: Envelope;
}

// TODO: check if everything needs a gain...
// TODO: how do we deal with Envelope??? Oscs need to know about it...
export interface ModulatorModule extends Module {
    evelopeId?: ID;
    generators: Record<ID, GeneratorModule>;
    gainModifiers: Record<ID, ModifierModule>;
    freqModifiers: Record<ID, ModifierModule>;
}

export interface SynthesisState {
    rootModuleId: RootModuleId;
    modules: Record<string, Module>;
    updated: string[];
}

const rootModule: Module = {
    id: ROOT_MODULE_ID,
    parentModuleId: ROOT_MODULE_ID,
    childModuleIds: [],
};
const initialState: SynthesisState = {
    rootModuleId: ROOT_MODULE_ID,
    modules: { [ROOT_MODULE_ID]: rootModule },
    updated: [],
};
export const synthesisSlice = createSlice({
    name: 'synthesis',
    initialState,
    reducers: {
        addModule: (state, action: PayloadAction<Module>): void => {
            const { id, parentModuleId } = action.payload;
            state.modules[id] = action.payload;
            state.modules[parentModuleId].childModuleIds.push(id);
            state.updated.push(id);
            state.updated.push(parentModuleId);
        },
    },
});

export const { addModule } = synthesisSlice.actions;
export const getModules = () =>
    useAppSelector(({ synthesis }) => synthesis.modules);
export const getModule = (id: string) =>
    useAppSelector(({ synthesis }) => synthesis.modules[id]);
export const getRootModuleId = () =>
    useAppSelector(({ synthesis }) => synthesis.rootModuleId);

export default synthesisSlice.reducer;
